:root {
  --primary-color: #4a90e2;
  --bg-color: #f5f7fa;
  --text-color: #333;
  --btn-hover: #357ab8;
}

* {
  box-sizing: border-box;
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
}

.container {
  text-align: center;
  background: #fff;
  border-radius: 10px;
  width: 100%;
  max-width: 400px;
  padding: 2rem;
  box-shadow: 0 4px 12px #0000001a;
}

.title {
  color: var(--primary-color);
  margin-bottom: 1.5rem;
  font-size: 1.75rem;
}

.upload-form {
  justify-content: center;
  align-items: center;
  gap: .5rem;
  margin-bottom: 1rem;
  display: flex;
}

.file-input-wrapper {
  cursor: pointer;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.file-input {
  opacity: 0;
  cursor: pointer;
  font-size: 100px;
  position: absolute;
  top: 0;
  left: 0;
}

.file-label {
  background-color: var(--primary-color);
  color: #fff;
  text-align: center;
  cursor: pointer;
  border-radius: 5px;
  width: 200px;
  padding: .5rem 1rem;
  transition: background-color .2s;
  display: inline-block;
}

.file-label:hover {
  background-color: var(--btn-hover);
}

.btn.upload-btn {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: .6rem 1.2rem;
  transition: background-color .2s;
}

.btn.upload-btn:hover {
  background-color: var(--btn-hover);
}

.status {
  margin-top: 1rem;
  font-weight: bold;
}

.link a {
  color: var(--primary-color);
  text-decoration: none;
}

.link a:hover {
  text-decoration: underline;
}

.stats {
  color: #666;
  margin-top: 1rem;
  font-size: .9rem;
}

.progress-container {
  background: #eee;
  border-radius: 5px;
  width: 100%;
  height: 10px;
  margin-top: 1rem;
  overflow: hidden;
}

.progress-bar {
  background-color: var(--primary-color);
  width: 0%;
  height: 100%;
  transition: width .2s;
}

.modal {
  z-index: 1000;
  background: #00000080;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal.active {
  justify-content: center;
  align-items: center;
  display: flex;
}

.modal-content {
  text-align: center;
  background: #fff;
  border-radius: 8px;
  min-width: 280px;
  max-width: 90%;
  padding: 1.5rem;
  position: relative;
}

.modal-btn {
  background: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin: .5rem;
  padding: .5rem 1rem;
  transition: background .2s;
}

.modal-btn:hover {
  background: var(--btn-hover);
}

.modal-close {
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1.2rem;
  position: absolute;
  top: .5rem;
  right: .5rem;
}

.toast {
  background: var(--primary-color);
  color: #fff;
  opacity: 0;
  z-index: 1001;
  border-radius: 4px;
  padding: .5rem 1rem;
  transition: opacity .3s;
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.toast.show {
  opacity: 1;
}
